$unit: 4px;

$min_width: 320;

$header_height: 76;
$header_height_px: calc( $header_height * 1px );

$border_radius: 8px;

$header_height_maxW_768: 64;
$header_height_maxW_768_px: calc( $header_height_maxW_768 * 1px );

$default_component_margin_px: 144px;
