$column: 128;
$gutter: 24;
$column_abs: calc( $column * 1px );
$gutter_abs: calc( $gutter * 1px );
$margin: 64;
$margin_px: calc( $margin * 1px );
$width: calc( ( $column_abs * 12 ) + ( $gutter_abs * ( 12 - 1 ) ) );
$column_count: 12;
$gutter_count: calc( $column_count - 1 );

$width_content: calc( ( $column * $column_count ) + ( $gutter * $gutter_count ) );
$width_content_px: calc( $width_content * 1px );

