.order-form__field--slider > .slider-output > span,
.order-form__field--slider > .slider-output output {
  font-family: "Lato";
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #272733;
  color: #FFFFFF;
}
.order-form__field--button {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}
@media (max-width: 768px) {
  .order-form__field--button {
    order: 6;
  }
}
.order-form__field--button > .btn {
  display: block;
  width: 100%;
  flex: 0 0 calc(33% - 30px);
  margin-right: 30px;
}
@media (max-width: 768px) {
  .order-form__field--button > .btn {
    flex: 0 0 100%;
    margin-right: 0;
  }
}

.choices[data-type*=select-one]:after {
  content: "";
  height: 20px;
  width: 20px;
  position: absolute;
  right: 11.5px;
  top: 50%;
  pointer-events: none;
  border: none;
  background-image: url(../img/svg/arrow.svg);
  background-repeat: no-repeat;
  background-size: 12px 8px;
  background-position: center;
  transform: translateY(-45%);
  overflow-y: hidden;
}

.choices[data-type*=select-one].is-open:after {
  background-image: url(../img/svg/arrow2.svg);
  margin-top: -3px;
}

.choices__list--single {
  padding: 0;
}

.choices__inner {
  font-family: "Lato";
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #272733;
  width: 100%;
  background: rgba(255, 255, 255, 0.85);
  border: 1px solid #FFFFFF;
  border-radius: 3px;
  outline: none;
  padding: 12px 10px;
}

.is-open .choices__inner {
  font-family: "Lato";
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #272733;
  color: #FFFFFF;
  padding: 10px 10px;
  border-radius: 3px 3px 0 0;
  background: rgba(61, 64, 80, 0.85);
  border: 1px solid rgba(61, 64, 80, 0.85);
  overflow: hidden;
}

.choices__list {
  max-height: 180px;
  overflow: auto;
}
.choices__list.custom-scroll::-webkit-scrollbar-track {
  -webkit-box-shadow: none;
  background-color: #3D4050;
}
.choices__list.custom-scroll::-webkit-scrollbar {
  width: 7px;
  background-color: #3D4050;
}
.choices__list.custom-scroll::-webkit-scrollbar-thumb {
  background-color: #3E9CDC;
  border-radius: 3px;
}

.choices__list--dropdown .choices__item {
  font-family: "Lato";
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #272733;
  color: #FFFFFF;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted {
  font-family: "Lato";
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #272733;
  background-color: transparent;
  color: #3E9CDC;
}

.choices__item--selectable {
  background: transparent;
}

.is-open .choices__list--dropdown {
  border: 1px solid rgba(61, 64, 80, 0.85);
  background-color: #3d4050;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted:after {
  display: none;
}

.just-validate-error-label {
  margin-top: 5px;
  font-family: "Lato";
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #FFFFFF;
}

.page__main {
  padding-top: 72px;
